import {
    PnpNode
} from "sp-pnp-node";
import {
    sp
} from "@pnp/sp";


function deploy(settings) {
    sp.setup({
        sp: {
            fetchClientFactory: () => new PnpNode(settings),
            baseUrl: settings.siteUrl
        }
    });

    sp.web.lists.getByTitle("AppConfig").items.get().then((items) => {
        console.log(items);
    });
}

export default deploy;